import { Box } from "@rp/react/web/mui/core"
import { ArrowDownward, ArrowUpward } from "@rp/react/web/mui/icons"
import { WatchlistDataTableColoredCell } from "../../WatchlistDataTableCells/WatchlistDataTableColoredCell"
import { genericColumn } from "../../genericColumn"

function abnormalMediaAttention(tableName: string) {
  const title = "Media Buzz"
  return genericColumn({
    endpoint: "companySentiment",
    columnKeys: ["abnormalMediaAttention"],
    size: 180,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip:
      "Indicates whether a company is receiving more or less media attention than usual, based on recent trends.",
    cell: info => {
      const rawValue =
        info.row.original.financialData.companySentiment?.data?.abnormalMediaAttention ?? 0
      const isAverage = rawValue >= -0.51 && rawValue <= 0.51
      return (
        <WatchlistDataTableColoredCell
          queryResult={info.row.original.financialData.companySentiment}
          tooltip={"Explain the Abnormal Volume"}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `Whatâ€™s causing abnormal media attention for ${info.row.original.entityInfo.name} recently?`,
              value: info.row.original.financialData.companySentiment?.data?.abnormalMediaAttention,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={isAverage ? 0 : rawValue}
        >
          <Box component="span" sx={{ display: "flex", gap: 0.5, alignItems: "center" }}>
            {rawValue > 0.7 && (
              <>
                Abnormally High
                <ArrowUpward sx={{ fontSize: 16 }} />
              </>
            )}
            {rawValue > 0.51 && rawValue <= 0.7 && (
              <>
                High
                <ArrowUpward sx={{ fontSize: 16 }} />
              </>
            )}
            {isAverage && <>Average</>}
            {rawValue < -0.51 && rawValue >= -0.7 && (
              <>
                Low
                <ArrowDownward sx={{ fontSize: 16 }} />
              </>
            )}
            {rawValue < -0.7 && (
              <>
                Abnormally Low
                <ArrowDownward sx={{ fontSize: 16 }} />
              </>
            )}
          </Box>
        </WatchlistDataTableColoredCell>
      )
    },
  })
}

export { abnormalMediaAttention }
